import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Consumer } from './context';

class DefaultConsumer extends Component {
    componentDidMount() {
        this.props.fetch();
    }

    render() {
        console.log('this.props.loading', this.props.loading);
        return (
            <div>
                <h2>Default consumer</h2>
                {this.props.data.map((val) => {
                    return (
                        <div key={val._id}>
                            <div >
                                name: {val.name}
                            </div>
                        </div>
                    );
                })}
            </div>
        );
    }
}

DefaultConsumer.propTypes = {
    loading: PropTypes.bool.isRequired,
    data: PropTypes.arrayOf(PropTypes.shape({})).isRequired
};

class ProjectsConsumer extends Component {
    render() {
        const { render } = this.props;
        return (
            <Consumer>
                {(props) => {
                    if (typeof render === 'function') {
                        return render(props);
                    }
                    return (
                        <DefaultConsumer {...props} />
                    );
                }}
            </Consumer>
        );
    }
}

ProjectsConsumer.defaultProps = {
    render: null
};

ProjectsConsumer.propTypes = {
    render: PropTypes.func
};

export default ProjectsConsumer;
